label {
  width: 100%;
  display: block;
  margin: 16px 0 4px 0; 
}

input {
  width: 100%;
  line-height: 24px;
  outline-color: #607d8b;
}

h3{
  text-align: right;
  color: #607d8b;
  margin-bottom: 20px;
  text-decoration: underline;
}

button{
  padding: 8px 16px;
  outline: none;
  background: #607d8b;
  color: white;
  font-weight: bold;
  cursor: pointer;
  margin: 16px auto;
  display: block;
  font-size: 16px;
  width: 100%;
  font-family: monospace;
}

body {
  font-family: monospace;
  color: #454545;
}